
.component-wrapper {
  margin-top: 20px;
  /* 组件展示页模块 */
}
.component-wrapper .component-nav {
  float: left;
  width: 200px;
}
.component-wrapper .component-nav-item {
  line-height: 30px;
  font-size: 14px;
  color: #333;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.component-wrapper .component-nav-item a {
  display: block;
}
.component-wrapper .comp-demo-page {
  float: right;
  width: 880px;
}
.component-wrapper .comp-demo-page .comp-title {
  line-height: 28px;
  font-size: 24px;
  color: #222;
}
.component-wrapper .comp-demo-page .comp-title:hover .comp-block-anchor {
  opacity: 1;
}
.component-wrapper .comp-demo-page .comp-desc {
  line-height: 20px;
  margin-top: 5px;
  font-size: 14px;
  color: #333;
}
.component-wrapper .comp-demo-page .comp-block-title {
  margin-top: 24px;
  font-size: 20px;
  color: #222;
}
.component-wrapper .comp-demo-page .comp-block-title:hover .comp-block-anchor {
  opacity: 1;
}
.component-wrapper .comp-demo-page .comp-block-desc {
  line-height: 20px;
  margin-top: 5px;
  font-size: 14px;
  color: #333;
}
.component-wrapper .comp-demo-page .comp-block-anchor {
  margin-left: 4px;
  color: #00a1d6;
  opacity: 0;
  transition: opacity .2s ease;
}
.component-wrapper .comp-demo {
  margin: 20px 0;
  border: 1px solid #f1f3f7;
  border-radius: 4px;
}
.component-wrapper .comp-demo .demo {
  padding: 20px;
  border-bottom: 1px solid #f1f3f7;
}
.component-wrapper .comp-demo .source {
  padding: 20px;
  background-color: #f9f9f9;
}



