
.nav-wrapper {
  background-color: #00a7de;
  color: #fff;
}
.nav-wrapper .nav-title {
  float: left;
  line-height: 60px;
  font-size: 24px;
}
.nav-wrapper .nav-title a:hover {
  color: #fff;
}
.nav-wrapper .nav-links {
  float: right;
}
.nav-wrapper .nav-links li {
  position: relative;
  float: left;
}
.nav-wrapper .nav-links a {
  display: block;
  line-height: 20px;
  width: 50px;
  padding: 20px 0;
  font-size: 14px;
  text-align: center;
  cursor: pointer;
}
.nav-wrapper .nav-links a:hover {
  color: #f2f2f2;
}
.nav-wrapper .nav-links a.active-link:after {
  content: '';
  position: absolute;
  z-index: 1;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0;
  border-top: 4px solid #e6e6e6;
}



